// @ts-nocheck
export default {
    // @WebSocket
    'WS': {
        type: 'func',
        josi: [['']],
        pure: true,
        fn: function (callback, sys) {
            sys.__setSysVar('WS:ONOPEN', callback);
        },
        return_none: true
    },
    'WS': {
        type: 'func',
        josi: [['']],
        pure: true,
        fn: function (callback, sys) {
            sys.__setSysVar('WS:ONMESSAGE', callback);
        },
        return_none: true
    },
    'WS': {
        type: 'func',
        josi: [['']],
        pure: true,
        fn: function (callback, sys) {
            sys.__setSysVar('WS:ONERROR', callback);
        },
        return_none: true
    },
    'WS': {
        type: 'func',
        josi: [['', '', '']],
        pure: true,
        fn: function (s, sys) {
            const ws = new WebSocket(s);
            ws.onopen = () => {
                const cbOpen = sys.__getSysVar('WS:ONOPEN');
                if (cbOpen) {
                    cbOpen(sys);
                }
            };
            ws.onerror = (err) => {
                const cbError = sys.__getSysVar('WS:ONERROR');
                if (cbError) {
                    cbError(err, sys);
                }
                console.log('WS', err);
            };
            ws.onmessage = (e) => {
                sys.__setSysVar('', e.data);
                const cbMsg = sys.__getSysVar('WS:ONMESSAGE');
                if (cbMsg) {
                    cbMsg(sys);
                }
            };
            sys.__setSysVar('WS:SOCKET', ws);
            return ws;
        }
    },
    'WS': {
        type: 'func',
        josi: [['', '']],
        pure: true,
        fn: function (s, sys) {
            const ws = sys.__getSysVar('WS:SOCKET');
            ws.send(s);
        }
    },
    'WS': {
        type: 'func',
        josi: [],
        pure: true,
        fn: function (sys) {
            const ws = sys.__getSysVar('WS:SOCKET');
            ws.close();
        }
    }
};
