export default {
    'meta': {
        type: 'const',
        value: {
            pluginName: 'plugin_promise', // 
            description: 'promise', // 
            pluginVersion: '3.6.0', // 
            nakoRuntime: ['wnako', 'cnako'], // 
            nakoVersion: '^3.6.0' // 
        }
    },
    '': {
        type: 'func',
        josi: [],
        pure: true,
        fn: function (sys) {
            if (sys.__promise == null) {
                sys.__promise = {
                    setLastPromise: function (promise) {
                        sys.__setSysVar('', promise);
                        return promise;
                    }
                };
            }
        }
    },
    // @
    '': { type: 'const', value: '' }, // @
    // @
    '': {
        type: 'func',
        josi: [['', '']],
        pure: true,
        fn: function (callback, sys) {
            return sys.__promise.setLastPromise(new Promise((resolve, reject) => {
                return callback(resolve, reject);
            }));
        },
        return_none: false
    },
    '': {
        type: 'func',
        josi: [[''], ['', '', '']],
        pure: true,
        fn: function (callback, promise, sys) {
            return sys.__promise.setLastPromise(promise.then((result) => {
                sys.__setSysVar('', result);
                return callback(result);
            }));
        },
        return_none: false
    },
    '': {
        type: 'func',
        josi: [[''], ['', '', '']],
        pure: true,
        fn: function (cbFunc, promise, sys) {
            return sys.__promise.setLastPromise(promise.then((result) => {
                sys.__setSysVar('', result);
                return cbFunc(true, result, sys);
            }, (reason) => {
                sys.__setSysVar('', reason);
                return cbFunc(false, reason, sys);
            }));
        },
        return_none: false
    },
    '': {
        type: 'func',
        josi: [[''], ['', '', '']],
        pure: true,
        fn: function (callback, promise, sys) {
            return sys.__promise.setLastPromise(promise.catch((err) => {
                sys.__setSysVar('', err);
                return callback(err);
            }));
        },
        return_none: false
    },
    '': {
        type: 'func',
        josi: [[''], ['', '', '']],
        pure: true,
        fn: function (callback, promise, sys) {
            return sys.__promise.setLastPromise(promise.finally(() => {
                return callback();
            }));
        },
        return_none: false
    },
    '': {
        type: 'func',
        josi: [['', '']],
        pure: true,
        fn: function (...args) {
            const sys = args.pop();
            return sys.__promise.setLastPromise(Promise.all(args));
        },
        return_none: false
    }
};
